import { fromJS } from 'immutable';
import {
  SET_HOME_DATA
} from './constants';

const defaultState = fromJS({
  homePagerIndex: 0,
  homePagerAllNum: 0,
  data: []
});

export default (state = defaultState, {type, value}) => {
  switch(type) {
    case SET_HOME_DATA:
      return state.merge({
        data: fromJS(value),
        homePagerAllNum: value.length,
      });
    default:
      return state;
  }
};
