import { fromJS } from 'immutable';
import { CHANGE_PAGER_INDEX } from './constants';

const defaultState = fromJS({
  index: 1
});

export default (state = defaultState, {type, value}) => {
  switch (type) {
    case CHANGE_PAGER_INDEX:
      return state.set('index', value);
    default:
      return state;
  }
};
