import React, { Fragment, lazy, Suspense } from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import store from './store';
import { GlobalStyle } from './static/styles/default';
import Header from './common/header'
const Home = lazy(() => import('./pages/home'));
const Project = lazy(() => import('./pages/project'));
const NewHand = lazy(() => import('./pages/new_hand'));
const Contribute = lazy(() => import('./pages/contribute'));
const LeaveMessage = lazy(() => import('./pages/leave_message'));
const Intergral = lazy(() => import('./pages/intergral'));

ReactDOM.render(
  <Fragment>
    <GlobalStyle />
    <Provider store={store}>
      <Router>
        <Suspense fallback={ <div>Loading...</div> }>
          <Header />
          <Switch>
            <Route path="/" exact render={() => <Home /> } />
            <Route path="/project" render={() => <Project />} />
            <Route path="/new_hand" render={() => <NewHand />} />
            <Route path="/contribute" render={() => <Contribute />} />
            <Route path="/leave_message" render={() => <LeaveMessage />} />
            <Route path="/intergral" render={() => <Intergral />} />
            <Route path='*' render={ () => <div>404!</div> } />
          </Switch>
        </Suspense>
      </Router>
    </Provider>
  </Fragment>,
  document.getElementById('root')
);

