import React, { Component } from 'react';
import HeaderUI from './HeaderUI';

class CommonHeader extends Component {

  state = {
    tabList: [{
      name: '首页',
      path: '/'
    }, {
      name: '精品教程',
      path: '/project'
    }, {
      name: '新手必看',
      path: '/new_hand'
    }, {
      name: '投稿指南',
      path: '/contribute'
    }, {
      name: '留言反馈',
      path: '/leave_message'
    }, {
      name: '积分获取',
      path: '/intergral'
    }],
    activePage: 0,
    isShowSmallBar: false
  };

  render () {
    return (
      <HeaderUI
        { ...this.state }
        handleLinkClick={ this.handleLinkClick }
        handleKeyUp={ this.handleKeyUp } 
        handleButtonClick={ this.handleButtonClick }
      />
    )
  }

  handleLinkClick = (idx, other_msg) => {
    console.log(idx, other_msg)
    this.setState(() => ({
      activePage: idx
    }))

    if (other_msg) {
      this.handleButtonClick()
    }
  }

  handleKeyUp = (search_msg) => {
    console.log(search_msg.replace(/\s*/g, ''))
  }

  handleButtonClick = () => {
    this.setState(({isShowSmallBar}) => ({
      isShowSmallBar: !isShowSmallBar
    }))
  }
};

export default CommonHeader;
