import styled from 'styled-components';

export const Wrapper = styled.div`
  position: relative;
`;

export const Header = styled.header`
  display: flex;
  justify-content: space-between;
  padding: 0 50px;
  height: 54px;
  line-height: 54px;
  box-shadow: 0 4px 8px 0 rgba(7, 17, 27, 0.1);
  h1 {
    font-size: 20px;
  }
  .person-msg {
    display: flex;
    justify-content: flex-end;
    width: 200px;
    img {
      margin: 10px 0 0 10px;
      height: 34px;
      border-radius: 50%;
      vertical-align: top;
      cursor: pointer;
    }
    .remind {
      position: relative;
      padding: 0 20px;
      color: #787d82;
      i {
        font-size: 20px;
      }
      &:hover {
        color: #f01414;
        cursor: pointer;
      }
      &::after {
        position: absolute;
        content: '';
        right: 14px;
        top: 14px;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: #f01414;
      }
    }
  }

  @media screen and (min-width: 990px) and (max-width: 1200px) {
    padding: 0 50px;
  }
  @media screen and (min-width: 768px) and (max-width: 989px) {
    padding: 0 5%;
  }
  @media screen and (max-width: 767px) {
    padding: 0 20px;
  }
`;

export const Tab = styled.div`
  padding: 10px 50px;
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  .nav-bar {
    float: left;
    display: flex;
    justify-content: flex-start;
    a {
      padding: 0 10px;
      line-height: 30px;
      letter-spacing: 1px;
      &:last-child {
        color: #f01414;
      }
      &.active {
        background: #f2f2f2;
        border-radius: 4px;
      }
    }
  }
  .search-area {
    position: relative;
    float: right;
    width: 240px;
    height: 29px;
    line-height: 1;
    border-bottom: 1px solid #ccc;
    .search {
      height: 100%;
      input {
        float: left;
        box-sizing: border-box;
        padding: 0 2px;
        width: 80%;
        height: 100%;
        outline: none;
        border: none;
      }
      button {
        float: right;
        margin: 6px;
        width: 10%;
        background: transparent;
        font-weight: 600;
        cursor: pointer;
      }
    }
  }

  @media screen and (min-width: 990px) and (max-width: 1200px) {
    padding: 0 50px;
  }
  @media screen and (min-width: 768px) and (max-width: 989px) {
    padding: 0 5%;
    .search-area {
      display: none;
    }
    .nav-bar ul {
      float: none;
      justify-content: space-between;
    }
  }
  @media screen and (max-width: 767px) {
    /* padding: 0 20px; */
    display: none;
  }
`;

export const SmallTab = styled.div`
  position: absolute;
  top: 64px;
  left: 0;
  right: 0;
  background: #fff;
  display: none;
  @media screen and (max-width: 767px) {
    display: block;
    &.show {
      div {
        height: 216px;
      }
    }
    button {
      margin: 0 0 20px 20px;
      padding: 8px 10px;
      border: 1px solid #ccc;
      border-radius: 4px;
      background: #fff;
      cursor: pointer;
    }
    div {
      height: 0;
      overflow: hidden;
      transition: .3s;
      a {
        display: block;
        padding: 0 10px;
        line-height: 30px;
        letter-spacing: 1px;
        &:last-child {
          color: #f01414;
        }
        &.active {
          background: #f2f2f2;
          border-radius: 4px;
        }
      }
    }
  }
`
