import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import $avator from '../../static/images/avatar.jpg';
import {
  Wrapper,
  Header,
  Tab,
  SmallTab
} from './style';

class HeaderUI extends Component {
  render () {
    const { tabList, activePage, isShowSmallBar, handleLinkClick, handleKeyUp, handleButtonClick } = this.props;

    return (
      <Wrapper>
        <Header>
          <h1>资源分享</h1>
          <div className="person-msg">
            <div className="remind">
              <i className="iconfont">&#xe621;</i>
            </div>
            <img src={$avator} alt="资源分享用户头像"/>
          </div>
        </Header>

        <Tab>
          <div className="nav-bar">
            {
              tabList.map(({name, path}, idx) => 
                <Link
                  key={name}
                  to={path}
                  className={activePage === idx ? 'active' : undefined}
                  onClick={() => { handleLinkClick(idx) }}
                > {name} </Link>
              )
            }
          </div>
          <div className="search-area">
            <div className="search">
              <input
                ref = {input => this.input = input}
                type="text"
                spellCheck={false}
                onKeyUp={e => { e.keyCode === 13 && handleKeyUp(e.target.value) }}
              />
              <button onClick={() => { handleKeyUp(this.input.value) }}>
                <i className="iconfont">&#xe602;</i>
              </button>
            </div>
          </div>
        </Tab>

        <SmallTab className={isShowSmallBar ? 'show' : undefined}>
          <button onClick={handleButtonClick}>
            <i className="iconfont">&#xe645;</i>
          </button>
          <div>
            {
              tabList.map(({name, path}, idx) => 
                <Link
                  key={name}
                  to={path}
                  className={activePage === idx ? 'active' : undefined}
                  onClick={() => { handleLinkClick(idx, 'smallBar') }}
                > {name} </Link>
              )
            }
          </div>
        </SmallTab>
      </Wrapper>
    )
  }
  
}

export default HeaderUI;
